export default {
	data() {
		return {
			sexBg:'rgba(255,93,91,1)',//性别颜色
			addHeight:'',			  //add板块高度
			animationData:{},		  //动画
			animationData1:{},		  //动画
			animationData2:{},		  //动画
			animationData3:{},		  //动画
			animationData4:{},		  //动画
			isAdd:false,
			myname:'熊猫',
			user:{
				name:'大鱼吃小鱼',
				nick:'小鱼',
				intr:'小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫,小鱼吃小熊猫'
			}
		};
	},
	onReady:function(){
		this.getElementStyle()
	},
	methods:{
		//返回首页
		backOne:function(){
			uni.navigateBack({
			    delta: 1
			});
		},
		//获取页面高度
		getElementStyle:function(){
			const query = uni.createSelectorQuery().in(this);
			query.select('.bg').boundingClientRect(data => {
			   console.log("得到布局位置信息" + JSON.stringify(data));
			  // console.log("节点离页面顶部的距离为" + data.top);
			  this.addHeight = data.height-(data.height/3.5);
			}).exec();
		},
		//添加好友动画
		addFriendAnimat:function(){
			this.isAdd = !this.isAdd
			
			var animation = uni.createAnimation({
			  duration: 300,
			  timingFunction: "ease",
			})
			var animation1 = uni.createAnimation({
			  duration: 300,
			  timingFunction: "ease",
			})
			var animation2 = uni.createAnimation({
			  duration: 300,
			  timingFunction: "ease",
			})
			var animation3 = uni.createAnimation({
			  duration: 300,
			  timingFunction: "ease",
			})
			var animation4 = uni.createAnimation({
			  duration: 300,
			  timingFunction: "ease",
			})
			if(this.isAdd){
				animation.bottom(0).step()
				animation1.bottom(0).step()
				animation2.width(120).height(120).step()
				animation3.opacity(0).step()
				animation4.backgroundColor('rgba(255,228,49,0.6)').step()
			}else{
				animation.bottom(-this.addHeight).step()
				animation1.bottom(-100).step()
				animation2.width(200).height(200).step()
				animation3.opacity(1).step()
				animation4.backgroundColor('rgba(255,228,49,0)').step()
			}
			this.animationData = animation.export()
			this.animationData1 = animation1.export()
			this.animationData2 = animation2.export()
			this.animationData3 = animation3.export()
			this.animationData4 = animation4.export()
		}
	}
	
}
